@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


html, body, #root, .App{
    font-family: 'Roboto Mono', monospace;
    height: 100%;
}
.App{
    background: #eee;
}
.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
  }
  .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 300px;
    height: 300px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid blue;
    border-color: blue transparent blue transparent;
    -webkit-animation: lds-dual-ring 2s linear infinite;
            animation: lds-dual-ring 2s linear infinite;
  }
  @-webkit-keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

  .add-board{
    cursor: pointer;
  }
  
.card-board{
    transition: 0.3s;
}
a:hover{
    text-decoration: none;
}
.card-board:hover{
    box-shadow: 0px 0px 19px 0px rgba(43,80,227,1);
}
/* .title-list */

.board-container{
    padding: 20px;
    display:flex;
    overflow: scroll;
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: url('https://newevolutiondesigns.com/images/freebies/cool-wallpaper-5.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}

.list-container{
    min-width: 300px;
    max-width: 300px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 20px;
    background:#cfd8dcbb;
    margin: 0 10px 0 10px;
    border-radius: 15px;
}

.card-container{
    font-family:'Rubik', sans-serif;
    background: #fff;
    width: 100%;
    padding:10px;
    border-radius: 10px;
    margin:10px 0 10px 0;
}
.add-card,.add-list{
    text-align: center;
    color: #999;
    cursor: pointer;
}
.add-list{
    color: #000;
}
.textarea-card{
    width: 100%;
    height: 50px;
    resize: none;
}
.textarea-card:focus{
    outline: none;
}
.btn-card{
    font-weight: normal;
    text-transform: none;
}
.hidden{
    display: none;
}

.form-add-list{
    width: 300px;
}

.title-list{
    font-size:30px;
    width: 100%;
    height: 2em;
    background: rgba(0,0,0,0);
    border:0;
    outline: none;
    resize: none;
    text-transform: uppercase;
    font-family: 'Roboto Mono', monospace;
    border-radius: 15px;

    transition: all 0.3s;
}
.title-list:focus{
    background: #fff;
}
