@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,500&display=swap');/* .title-list */

.board-container{
    padding: 20px;
    display:flex;
    overflow: scroll;
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: url('https://newevolutiondesigns.com/images/freebies/cool-wallpaper-5.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}

.list-container{
    min-width: 300px;
    max-width: 300px;
    height: fit-content;
    padding: 20px;
    background:#cfd8dcbb;
    margin: 0 10px 0 10px;
    border-radius: 15px;
}

.card-container{
    font-family:'Rubik', sans-serif;
    background: #fff;
    width: 100%;
    padding:10px;
    border-radius: 10px;
    margin:10px 0 10px 0;
}
.add-card,.add-list{
    text-align: center;
    color: #999;
    cursor: pointer;
}
.add-list{
    color: #000;
}
.textarea-card{
    width: 100%;
    height: 50px;
    resize: none;
}
.textarea-card:focus{
    outline: none;
}
.btn-card{
    font-weight: normal;
    text-transform: none;
}
.hidden{
    display: none;
}

.form-add-list{
    width: 300px;
}

.title-list{
    font-size:30px;
    width: 100%;
    height: 2em;
    background: rgba(0,0,0,0);
    border:0;
    outline: none;
    resize: none;
    text-transform: uppercase;
    font-family: 'Roboto Mono', monospace;
    border-radius: 15px;

    transition: all 0.3s;
}
.title-list:focus{
    background: #fff;
}